define('apps/wysiwyg/views/ListButton',['require','underscore','./Command'],function(require) {

var _ = require("underscore");
var CommandView = require("./Command");

return CommandView.extend({

  onEditorReady: function() {
    var selector = this.model.get("selector");

    this.editor.selection.selectorChanged(selector, _.bind(function(state) {
      this.el.setAttribute("aria-selected", state);
    }, this));
  }

});

});

