define('apps/wysiwyg/views/StandardButton',['require','underscore','./Command'],function(require) {

var _ = require("underscore");
var CommandView = require("./Command");

return CommandView.extend({

  onEditorReady: function() {
    var command = this.model.get("command");

    this.editor.formatter.formatChanged(command, _.bind(function(state) {
      this.el.setAttribute("aria-selected", state);
    }, this));
  }

});

});

