define('apps/recentActivity/App',['require','../helpers/extend','./collections/Activities','./views/Master'],function(require){

  // Uncomment to use mockjax
  // require('./mock')('/hc/api/internal/*');

  var App;
  var _extend = require('../helpers/extend');

  var Activites = require('./collections/Activities');
  var Master = require('./views/Master');

  App = function(options) {
    var urlFragments = [];

    this.options = _extend({
      el: '[data-app="recent-activity"]',
      root_url: '/hc/api/internal',
      ressource: 'recent_activities.json',
      content_type: null,
      content_id: null
    }, options || {});

    if (typeof(this.options.el) === 'string') {
      this.options.el = document.querySelector(this.options.el);
    }

    //Sanity check for root el
    if (typeof(this.options.el.innerHTML) !== 'string') {
      return; //Quit
    }

    //Build URL
    urlFragments.push(this.options.root_url);
    if (this.options.content_type && this.options.content_id) {
      urlFragments.push(this.options.content_type, this.options.content_id, this.options.ressource);
    } else {
      urlFragments.push(this.options.ressource);
    }

    //Collection instance
    this.activites = new Activites();
    this.activites.url = urlFragments.join('/');

    //View instance
    this.master = new Master({ collection: this.activites });

    //Attach to the dom
    this.options.el.appendChild(this.master.el);

    //Fetch data
    this.activites.fetch({
      reset: true,
      data: { page: 1 }
    });
  };

  return App;
});

