define('apps/wysiwyg/views/Dropdown',['require','backbone','./Command'],function(require) {

var Backbone = require("backbone");
var CommandView = require("./Command");

return Backbone.View.extend({

  tagName: "span",

  attributes: {
    "aria-haspopup": true,
    "aria-expanded": false
  },

  events: {
    "click > [role=button]": "toggleDropdown",
    "blur > [role=menu]": "closeDropdown",
  },

  initialize: function(options) {
    this.label = options.label;

    this.views = {
      toggle: this.buildToggle(),
      menu: this.buildMenu()
    };

    this.render();
  },

  buildToggle: function() {
    return new Backbone.View({
      tagName: "span",
      attributes: { role: "button" },
      className: "wysiwyg-icon-formats"
    });
  },

  buildMenu: function() {
    var menu = new Backbone.View({
      tagName: "ul",
      attributes: { role: "menu", "aria-hidden": true, tabindex: "-1" },
    });

    this.collection.each(function(command) {
      var view = new CommandView({ model: command });
      var text = command.get("label");
      var value = command.get("value");
      view.$el.text(text).attr("data-block", value);
      menu.$el.append(view.el);
    }, this);

    return menu;
  },

  render: function() {
    this.$el.append([
      this.views.toggle.el,
      this.views.menu.el
    ]);
  },

  toggleDropdown: function(event) {
    event.stopPropagation();

    this.$el.attr({ "aria-expanded": true });
    this.$("> [role=button]").attr({ "aria-selected": true });
    this.$("> [role=menu]").attr({ "aria-hidden": false }).focus();
  },

  closeDropdown: function() {
    this.$el.attr({ "aria-expanded": false });
    this.$("> [role=button]").attr({ "aria-selected": false });
    this.$("> [role=menu]").attr({ "aria-hidden": true });
  }

});

});

