/* global $zopim */
define('pages/enduser/common',['require','helpcenter-adapter','apps/modalForm','apps/snippets/dismissNotification','apps/snippets/avatarUrl','apps/snippets/preventEmptySearch','apps/snippets/formatDateTime','apps/UserDropdown','apps/dropdowns','apps/snippets/setupPartial','apps/snippets/zenmote','apps/snippets/setupSso'],function (require) {
  "use strict";

  var HelpCenter = require("helpcenter-adapter");
  var requestEmail = require("apps/modalForm");
  var dismissNotification = require("apps/snippets/dismissNotification");
  var avatarUrl = require("apps/snippets/avatarUrl");
  var preventEmptySearch = require("apps/snippets/preventEmptySearch");
  var formatDateTime = require("apps/snippets/formatDateTime");
  var UserDropdown = require("apps/UserDropdown");
  var dropdowns = require("apps/dropdowns");
  var setupPartial = require("apps/snippets/setupPartial");
  var zenmote = require("apps/snippets/zenmote");
  var setupSso = require("apps/snippets/setupSso");

  dismissNotification();
  avatarUrl();
  preventEmptySearch();
  formatDateTime();
  new UserDropdown();
  dropdowns();
  setupPartial();
  zenmote();
  setupSso();

  if (HelpCenter && HelpCenter.user) {

    var userRole = HelpCenter.user.role;
    // If the user logged in with twitter we don't have his email yet
    if (userRole !== "anonymous" && !HelpCenter.user.email) {
      requestEmail("/hc/user_email/new");
    }

    // If zopim is enabled and user is signed in as an enduser or anonymous
    if (HelpCenter.settings.zopim_enabled &&
        (userRole === "anonymous" || userRole === "end_user") &&
        typeof $zopim !== "undefined") {
      // Customize the zopim widget
      $zopim(function() {
        if (userRole === "end_user") {
          $zopim.livechat.setName(HelpCenter.user.name);
          $zopim.livechat.setEmail(HelpCenter.user.email);
        }
        $zopim.livechat.setLanguage(HelpCenter.current_session.locale);
      });
    }

  }

});

