/* global history */
/* 
 * This is a simpler replacement for rails/jquery-ujs for making unobtrusive
 * ajax links, which doesn't make any assumptions of the global namespace.
 *
 * Usage:
 *
 * Assuming an enpoint /what/ever returning some HTML: "<h1>Gday Mate!</h1>"
 *
 * Create an ajax link by specifying a data attribute "data-zenmote", with a
 * value indicating a target:
 *
 * <a href="/what/ever" data-zenmote="foobar">Sing Halleluja!</a>
 *
 * Create a "target" container for which you want the response
 * from /what/ever to replace its content:
 *
 * <div data-target="foobar">This div could use a title!</div>
 *
 * Clicking the link sends of an ajax request, and the code below replaces
 * the content of the target with the response:
 *
 * <div data-target="foobar"><h1>Gday Mate!</h1></div>
 *
 */
define('apps/snippets/zenmote',['domlib'], function($) {
  return function() {
    $(function() {
      $('body').on('click', 'a[data-zenmote]', function(e) {
        var $e = $(e.currentTarget);

        var url = $e.attr('href');
        var target = $e.data('zenmote');

        $.get(url, function(data)  {
          if(history && history.pushState) {
            history.pushState({}, '', url);
          }

          $('[data-zenmote-target="' + target + '"]').html(data);
        }, 'html');

        e.preventDefault();
      });
    });
  };
});

