define('apps/recentActivity/collections/Activities',['require','backbone','underscore','helpcenter-adapter','../models/Activity','../models/Pagination','../Mediator'],function(require){
  var Backbone = require('backbone');
  var _ = require('underscore');
  var helpCenter = require('helpcenter-adapter');

  var Activity = require('../models/Activity');
  var PaginationModel = require('../models/Pagination');
  var mediator = require('../Mediator').instance;

  return Backbone.Collection.extend({
    model: Activity,
    initialize: function(options) {
      options = options || {};
      this.url = options.url;
      this.pagination = new PaginationModel({
        per_page: 5
      });
    },
    fetchMore: function(options) {
      options = options || {};
      var nextPageIndex = this.pagination.get('page') + 1;

      if (this.pagination.get('page') === this.pagination.get('page_count')) {
        //The is no more to fetch! :-(
        return false;
      }

      if (this.first()) {
        options.data = { page: nextPageIndex, since: this.first().get('timestamp') };
      }

      return this.fetch(options);
    },
    fetch: function(options) {
      var self = this;
      options = options || {};
      options.data = options.data || {};
      options.data.page = options.data.page || this.pagination.get('page');
      options.data.per_page = options.data.per_page || this.pagination.get('per_page');
      options.data.locale = helpCenter.current_session.locale;

      options.error = function() { mediator.trigger('activities:loaderror', self); };
      options.complete = function() { mediator.trigger('activities:loadend', self); };

      mediator.trigger('activities:loadstart', self);

      Backbone.Collection.prototype.fetch.call(this, options);
    },
    parse: function(resp) {
      var obj = {};
      var activities = resp.activities || [];
      var paginationKeys = _.keys(this.pagination.attributes);

      _.each(paginationKeys, function(key) {
        obj[key] = resp[key];
      }, this);

      //Update the pagination
      this.pagination.set(obj);

      return activities;
    }
  });
});

