define('apps/asynchtmlSetup',['domlib', 'i18n', 'jquery.asynchtml'], function($, i18n) {
  'use strict';

  $(function(){
    $('[data-asynchtml]').asynchtml({
      linkSelector: 'a[data-asynchtml-ressource]',
      targetSelector: '[data-asynchtml-target]',
      sourceSelector: '.article-body',
      readmore: i18n.t('txt.help_center.javascripts.components_with_articles.read_more')
    });
  });

});

