define('apps/recentActivity/views/Activity',['require','backbone','underscore','i18n','moment','text!../templates/activity.html'],function(require) {
  var Backbone = require('backbone');
  var _ = require('underscore');
  var i18n = require('i18n');

  var moment = require('moment');
  var template = require('text!../templates/activity.html');

  return Backbone.View.extend({
    tagName: 'li',
    className: 'recent-activity-item',
    initialize: function() {
      this.template = _.template(template);
      this.render();
    },
    getTranslationForAction: function() {
      var action, mappedToCommentAdded, actionToTranslationKey, i18nKey;

      action = this.model.get('action');

      mappedToCommentAdded = {
        'article_comment_added': true,
        'post_comment_added': true,
        'answer_added': true
      };

      if (mappedToCommentAdded[ action ]) {
        actionToTranslationKey = 'comment.added';
      } else {
        actionToTranslationKey = action.replace('_', '.');
      }

      i18nKey = 'views.admin.shared.recent_activity.' + actionToTranslationKey;

      return i18n.t(i18nKey);
    },
    render: function() {
      var data, timestamp, time_text,
          crumbs, crumb, parent_url, parent_name;

      timestamp = this.model.get('timestamp');

      time_text = this.getTranslationForAction();
      time_text = time_text.replace('%{time}', moment(timestamp).fromNow());

      //Create breadcrumb data
      crumbs = this.model.get('breadcrumbs');
      crumb = _.last(crumbs);
      parent_url = crumb.url || '#';
      parent_name = crumb.name || '';

      //Concat data object for template
      data = _.extend({
        time_text: time_text,
        parent_name: parent_name,
        parent_url: parent_url,
      }, this.model.toJSON());

      //Use jQuery html santizer for injecting html
      this.$el.attr('data-recent-activity-action', data.action);
      this.$el.html( Backbone.$.parseHTML(this.template(data)) );
    }
  });
});

