define('apps/recentActivity/views/Activities',['require','backbone','./Activity'],function(require){
  var Backbone = require('backbone');
  var Activity = require('./Activity');

  return Backbone.View.extend({
    tagName: 'ul',
    className: 'recent-activity-list',
    initialize: function() {
      this.listenTo(this.collection, 'reset', this.render, this);
      this.listenTo(this.collection, 'add', this.onAdd, this);
      this.hide();
    },
    onAdd: function(model) {
      var activity = new Activity({ model: model });
      this.el.appendChild(activity.el);
    },
    hide: function() {
      this.$el.hide();
    },
    show: function() {
      this.$el.show();
    },
    render: function() {
      var fragment = document.createDocumentFragment();

      this.collection.each(function(model) {
        var activity = new Activity({ model: model });
        fragment.appendChild(activity.el);
      }, this);

      this.el.appendChild(fragment);

      this.show();
    }
  });
});

