define('apps/snippets/setupSso',['require','domlib','helpcenter-adapter','apps/sso/Host'],function(require) {
  var $ = require('domlib');
  var HelpCenter = require('helpcenter-adapter');
  var SSOHost = require('apps/sso/Host');
  var sso;

  return function() {
    if (!HelpCenter.sso.enabled) { return; }

    sso = new SSOHost({
      return_to: HelpCenter.sso.return_to,
      auth_domain: HelpCenter.sso.auth_domain,
      brand_id: HelpCenter.sso.brand_id,
      locale: HelpCenter.current_session.locale,
      token: HelpCenter.sso.email_token || HelpCenter.sso.password_token
    });

    $('body').on('click', '[data-sso-action]', function(e) {
      e.preventDefault();
      var action = this.getAttribute('data-sso-action');
      sso.load(action);
    });

    if (!!HelpCenter.sso.action) {
      sso.load(HelpCenter.sso.action);
    }
  };
});

