define('apps/recentActivity/Mediator',['require','backbone','underscore'],function(require) {
  var Backbone = require('backbone');
  var _ = require('underscore');
  var Mediator = function() {
    this._events = {};
  };

  _.extend(Mediator.prototype, Backbone.Events);

  Mediator.instance = new Mediator();

  return Mediator;
});

