define('apps/recentActivity/views/Loader',['require','backbone','../Mediator'],function(require){
  var Backbone = require('backbone');
  var mediator = require('../Mediator').instance;

  return Backbone.View.extend({
    className: 'recent-activity-loader',
    initialize: function() {
      this.hide();
      this.render();

      this.listenTo(mediator, 'activities:loadend', this.hide, this);
      this.listenTo(mediator, 'activities:loadstart', this.show, this);
    },
    hide: function() {
      this.$el.hide();
    },
    show: function() {
      this.$el.show();
    },
    render: function() {
      this.el.innerHTML = '...';
    }
  });
});

