// This plugin is simply a wrapper around fileupload.js.
// It basically sets up fileupload with all the callbacks required to make uploads work
// as they're supposed to for the Hep Center.
// The settings should be specified as data attributes in the input type file.

// Usage:
// $("input[type='file']").upload();

(function(root, factory) {
  if (typeof define === 'function' && define.amd) {
    // AMD. Register as an anonymous module.
    define('upload',[
      'jquery',
      'jquery.fileupload'
    ], factory);
  } else {
    // Browser globals
    factory(root.jQuery);
  }
}(this, function($) {

  var Upload = function(inputFile) {
    this.$uploader = $(inputFile);
    this.$dropzone = $("#" + this.$uploader.data("dropzone"));
    this.$error = $("#" + this.$uploader.data("error"));
    this.$pool = $("#" + this.$uploader.data("pool"));

    var templateHTML = $("#" + this.$pool.data("template")).html();
    this.$template = $($.trim(templateHTML));

    this.url = this.$uploader.data("create-url");
    this.name = this.$uploader.data("name");
    this.deleteConfirmMsg = this.$uploader.data("delete-confirm-msg");
    this.serialiazedObject = this.name.indexOf("_ids][]") == -1;

    this.init();
  };

  Upload.prototype = {

    init : function() {
      this.cleanup();
      this.populate();
      this.setup();
    },

    cleanup : function() {
      this.$pool.empty();
      this.$error.hide();
    },

    populate : function() {
      var uploads = this.$pool.data("uploads") || [],
          $uploads = $([]),
          _this = this;

      $.each(uploads, function(i, upload) {
        // Use 'display_file_name' preferably but always fallback to 'file_name'
        // since it's uncertain that there will always be a 'display_file_name' prop
        var $upload = _this.render(upload.display_file_name || upload.file_name);
        _this.setupload($upload, upload);
        $uploads = $uploads.add($upload);
      });

      this.append($uploads);
    },

    render : function(fileName) {
      var $upload = this.$template.clone();
      this.setLinkText($upload, fileName);
      this.setInputName($upload);
      return $upload;
    },

    append : function($uploads) {
      this.$pool.append($uploads);
    },

    setupload : function($upload, upload) {
      this.setLinkUrl($upload, upload.url);
      this.setPathUrl($upload, upload.url);
      this.setRemoveUrl($upload, upload.delete_url);
      this.setInputValue($upload, upload);
      this.setProgress($upload, 100);
      this.setRemoveEvent($upload);
    },

    setLinkText : function($upload, linkText) {
      $upload
        .find("[data-upload-link]")
        .text(linkText);
    },

    setInputName : function($upload) {
      $upload
        .find("input:hidden")
        .attr("name", this.name);
    },

    setLinkUrl : function($upload, url) {
      $upload
        .find("[data-upload-link]")
        .attr("href", url);
    },

    setPathUrl : function($upload, url) {
      $upload
        .find("[data-upload-path]")
        .text(url);
    },

    setRemoveUrl : function($upload, delete_url) {
      $upload
        .find("[data-upload-remove]")
        .data("delete-url", delete_url);
    },

    setInputValue : function($upload, upload) {
      var payload = this.serialiazedObject ? upload : upload.id;

      $upload
        .find("input:hidden")
        .val(JSON.stringify(payload));
    },

    setProgress : function($upload, percentage) {
      $upload
        .find("[data-upload-progress]")
        .css("width", percentage + "%");
    },

    setRemoveEvent : function($upload) {
      $upload
        .find("[data-upload-remove]")
        .click($.proxy(this.remove, this));
    },

    setup : function() {
      this.$uploader.fileupload({
        // CONFIG
        dropZone : this.$dropzone,
        pasteZone: null,
        url: this.url,
        dataType : "json",
        type: "POST",
        formData: [
          { name: 'authenticity_token', value: HelpCenter.current_session.csrf_token }
        ],
        paramName: "file",
        singleFileUploads: true,
        // CALLBACKS
        dragover : $.proxy(this.dragover, this),
        add      : $.proxy(this.add, this),
        progress : $.proxy(this.progress, this),
        done     : $.proxy(this.done, this),
        fail     : $.proxy(this.error, this)
      });
    },

    dragover : function(e) {
      var _this = this,
          timeout = window.dropZoneTimeout;

      if (!timeout) this.$dropzone.attr("data-hover", true);
      else clearTimeout(timeout);

      window.dropZoneTimeout = setTimeout(function () {
        window.dropZoneTimeout = null;
        _this.$dropzone.attr("data-hover", false);
      }, 100);
    },

    add : function(e, data) {
      var file = data.files[0];

      if (this.exists(file.name)) {
        this.flash("File already exists", file.name);
        return;
      }

      if (typeof(file.size) !== 'undefined') {
        if (!this.valid(file)) {
          this.flash("File invalid");
          return;
        }
      }

      var $upload = this.render(file.name);
      $upload.attr("aria-busy", true);
      data.context = $upload;
      this.append($upload);
      data.submit();
    },

    exists : function(fileName) {
      return this.$pool.find("a").filter(function() {
        return this.innerText === fileName;
      }).length ? true : false;
    },

    // This will only affect browsers with the File API
    valid : function(file) {
      return file.size > 0; // Ensure the file has a size
    },

    progress : function(e, data) {
      var $upload = data.context;
      if (!$upload) return;
      var percentage = parseInt(data.loaded / data.total * 100, 10);
      if (percentage <= 90) this.setProgress($upload, percentage);
    },

    done : function(e, data) {
      var $upload = data.context;
      this.setupload($upload, data.result);
      // wait one second to give some space for the progress animation to complete
      setTimeout(function() {
        $upload.attr("aria-busy", false);
      }, 1000);
    },

    remove : function(e) {
      var confirmMsg = this.deleteConfirmMsg;

      if (confirmMsg.length && !window.confirm(confirmMsg)) {
        return;
      }

      var $removeBtn = $(e.target),
          $upload = $removeBtn.closest("[data-upload-item]"),
          deleteUrl = $removeBtn.data("delete-url"),
          _this = this;

      this
        .destroy(deleteUrl)
        .done(function() {
          $upload.remove();
          _this.$uploader.trigger("fileuploadremove");
        });
    },

    destroy : function(url) {
      return $.post(url, { _method : "DELETE" });
    },

    error : function(event, data) {
      var $upload = data.context;
      $upload.remove();
      var details = data.jqXHR.responseJSON ? data.jqXHR.responseJSON[0] : null;

      this.flash(data.errorThrown, details);
    },

    flash : function(message, payload) {
      var text;

      switch(message) {
        case "Bad Request":
          text = I18n.t("txt.help_center.views.shared.upload_error.file_too_big");
          break;
        case "File too big":
          text = I18n.t("txt.help_center.views.shared.upload_error.file_too_big");
          break;
        case "File invalid":
          text = I18n.t("txt.help_center.views.shared.upload_error.file_invalid");
          break;
        case "File already exists":
          text = I18n.t('txt.help_center.views.shared.upload_error.file_already_exist',
            { filename: payload });
          break;
        case "Unprocessable Entity":
          text = I18n.t('txt.help_center.views.shared.upload_error.file_too_big_with_limit',
            { limit: payload.size_limit });
          break;
        default:
          text = I18n.t("txt.help_center.views.shared.upload_error.connection_lost");
      }

      this.$error.show()
        .find("[data-upload-error-message]")
        .text(text);
    }

  };

  $.fn.upload = function () {
    return this.each(function () {
      $.data(this, "upload", new Upload(this));
    });
  };

}));

