(function(root, factory) {
  if (typeof define === 'function' && define.amd) {
    // AMD. Register as an anonymous module.
    define('jquery.asynchtml',['jquery'], factory);
  } else {
    // Browser globals
    factory(root.jQuery);
  }
}(this, function($) {
  'use strict';

  $.fn.asynchtml = function(options) {
    var settings;

    settings = $.extend({
      linkSelector: 'a[data-asynchtml-ressource]',
      targetSelector: '[data-asynchtml-target]',
      sourceSelector: '.article-body',
      readmore: null
    }, options);

    this.each(function() {
      var $this = $(this);

      function clickFn(e) {
        var $this, $target, attr, isSelected, isLoaded;

        e.preventDefault();

        $this = $(e.currentTarget);
        $target = $this.parent().find(settings.targetSelector).first();
        attr = $this.attr('href');
        isSelected = ($this.attr('aria-selected') === 'true');
        isLoaded = !!$this.data('asynchtml.loaded');

        if (!isLoaded) {
          //Mark ressource as loaded
          $this.data('asynchtml.loaded', true);
          //Load article and inject it
          $target.load( [attr,settings.sourceSelector].join(' '), function() {
            if (!!settings.readmore) {
              $target.append('<a href="'+$this.attr('href')+'">'+settings.readmore+'</a>');
            }
          });
        }

        $this.attr('aria-selected', !isSelected);
      }

      //Initialize setup
      $this.find(settings.linkSelector).attr('aria-selected', false);
      //Bindings
      $this.on('click', settings.linkSelector, clickFn);
    });

    return this;
  };

}));

