define('pages/enduser/posts',['require','domlib','nesty','apps/snippets/setupTinyMCESlim','apps/snippets/populatePendingComments'],function (require) {

  var $ = require("domlib");
  var Nesty = require("nesty");
  var setupTinymceSlim = require("apps/snippets/setupTinyMCESlim");
  var populatePendingComments = require("apps/snippets/populatePendingComments");

  $(".form-field select").each(function() {
    new Nesty(this);
  });

  setupTinymceSlim({
    selector: "#community_post_details",
    toolbar: "formats | bold italic | bullist numlist indent outdent | image"
  });

  setupTinymceSlim({
    selector: "#community_comment_body",
    toolbar: "bold italic | bullist numlist indent outdent | image"
  });

  populatePendingComments();

});

