define('apps/UserDropdown',[
  "helpcenter-adapter",
  "domlib",
  "apps/notification/App",
  "i18n",
  "jquery.fileupload"
], function(HelpCenter, $, Notification, i18n) {

  var UserDropdown = function() {
    this.$userToggler = $("#user > [role=button]");
    this.$userMenu = $("#user-menu");
    this.$userDropdown = $("#user-dropdown");
    this.$userButton = $("#user");
    // profile
    this.$userProfile = $("#user-profile");
    this.$profileOpen = $("#profile-open");
    this.$profileForm = $("#profile-form");
    this.$profileFail = $("#profile-fail");
    this.$profileHeader = $("#profile-header");
    this.$profileEmail = $("#profile-email");
    this.$profileIdentities = $("#profile-identities");
    this.$profilePhotoInput = $("#profile-photo-input");
    this.loadingProfile = false;
    // password panel
    this.$userPassword = $("#user-password");
    this.$passwordChange = $("#password-change");
    this.$passwordForm = $("#password-form");
    this.$passwordFlash = $("#password-flash");
    this.$passwordSubmit = $("#password-submit");

    this.preloadGif();
    this.events();
  };

  UserDropdown.prototype = {

    preloadGif : function() {
      var image = new Image();
      image.src = "/hc/assets/loader.gif";
    },

    events : function() {
      this.$userToggler.click($.proxy(this.resetUserMenu, this));
      // profile
      this.$profileOpen.click($.proxy(this.openProfilePanel, this));
      this.$profileForm.submit(function() { return false; });
      this.$profilePhotoInput.click($.proxy(this.uploadAvatar, this));
      this.$profileForm.on("change", "input:not(:file), select", $.proxy(this.submitProfile, this));
      // password
      this.$passwordChange.click($.proxy(this.openPasswordPanel, this));
      this.$passwordSubmit.click($.proxy(this.submitPassword, this));
      this.$passwordForm.submit(function() { return false; });
    },

    // Resetting the user menu

    resetUserMenu : function() {
      this.resetProfilePanel();
      this.resetPasswordPanel();
      this.$userDropdown.removeAttr("style");
      this.$userMenu.show();
    },

    // Opening/closing the profile panel

    openProfilePanel : function(e) {
      e.stopPropagation();
      if (this.loadingProfile) { return; }

      this.startProfileLoader();

      this
        .getProfileData()
        .always($.proxy(this.clearProfileLoader, this))
        .fail($.proxy(this.showErrorMessage, this))
        .done($.proxy(this.unveilProfile, this));
    },

    startProfileLoader : function() {
      this.loadingProfile = true;
      this.$profileOpen.attr("data-loading", true);
    },

    getProfileData : function() {
      return $.get("/hc/user_profile", {}, null, "json");
    },

    clearProfileLoader : function() {
      this.loadingProfile = false;
      this.$profileOpen.attr("data-loading", false);
    },

    showErrorMessage : function () {
      this.$profileFail.show();
    },

    unveilProfile : function(data) {
      this.populateProfile(data);
      this.populateEmails(data);
      this.populateIdentities(data);
      this.show(this.$userProfile);
    },

    show : function($panel) {
      $.when(
        this.$userMenu.fadeOut(100),
        this.$userDropdown.animate({
          "min-height" : $panel.height(),
          "min-width" : $panel.width()
        }, 100)
      )
      .done(function() {
        $panel.fadeIn(100);
      });
    },

    populateProfile : function(data) {
      $("#profile-name").val(data.name);
      $("#user-name").text(data.name);
      $("#profile-phone").val(data.phone);
      $("#profile-time-zone").val(data.time_zone);
      $("#profile-photo img, #user-avatar").attr({
        src : data.photo && data.photo.url ?
          data.photo.url : "/hc/assets/user_avatar.png"
      });
    },

    populateEmails : function(data) {
      var _this = this,
          emails = this.templateDD(data.email);

      $.each(data.identities || [], function(i, identity) {
        if (identity.type === "email" && !identity.primary) {
          emails += _this.templateDD(identity.value);
        }
      });

      this.$profileEmail.append(emails);
    },

    templateDD : function(email) {
      return "<dd><a>" + email + "</a></dd>";
    },

    templateDT : function(label) {
      return "<dt><label>" + label + "</label></dt>";
    },

    populateIdentities : function(data) {
      var _this = this,
          identities = "";

      $.each(data.identities || [], function(i, identity) {
        if (identity.type === "email") { return; }
        identities += _this.templateDT(identity.type_name);
        identities += _this.templateDD(identity.value);
      });

      this.$profileIdentities.append(identities);
    },

    // Submitting the profile form

    uploadAvatar : function() {
      this.$profilePhotoInput.fileupload({
        autoUpload : true,
        url : this.getPath(this.$profileForm),
        type : "POST",
        dataType : "json",
        formData : [{
          name: "authenticity_token",
          value: HelpCenter.current_session.csrf_token
        },{
          name: "_method",
          value: "put"
        },{
          name: "utf8",
          value: ""
        }],
        start : $.proxy(this.startSubmitLoader, this, this.$profileForm),
        always : $.proxy(this.clearSubmitLoader, this, this.$profileForm),
        done : $.proxy(this.avatarUploaded, this)
      });
    },

    avatarUploaded : function(e, data) {
      this.populateProfile(data.result);
    },

    submitProfile : function() {
      this
        .submit(this.$profileForm)
        .done($.proxy(this.populateProfile, this));
    },

    resetProfilePanel : function() {
      this.$userProfile.hide();
      this.$profileFail.hide();
      this.$profileIdentities.empty();
      this.$profileEmail.find("dd").remove();
    },

    // Change password panel

    openPasswordPanel : function(e) {
      e.stopPropagation();
      this.show(this.$userPassword);
    },

    resetPasswordPanel : function() {
      this.$passwordFlash.hide();
      this.$userPassword.hide();
      this.$userPassword
        .find("input:password")
        .val("");
    },

    submitPassword : function(e) {
      e.preventDefault();

      this
        .submit(this.$passwordForm)
        .always($.proxy(this.submitPasswordFeedback, this));
    },

    submitPasswordFeedback : function(data, status) {
      var className = "password-flash-" + status,
          messages = data.responseText ? $.parseJSON(data.responseText) : data ,
          message = messages.join("<br>");

      if (status === "success") {
        this.$userDropdown.attr("aria-expanded", false);

        var notification = new Notification({
          type: "lock",
          icon: true,
          message: i18n.t("txt.help_center.controllers.user_passwords_controller.update.success")
        });
        notification.prependTo(document.body);
      } else {
        this.$passwordFlash
          .attr("class", className)
          .html(message)
          .fadeOut().fadeIn();
      }
    },

    // form submit

    submit : function($form) {
      var path = this.getPath($form),
          params = this.getParams($form);

      this.startSubmitLoader($form);

      return $.post(path, params, null, "json" )
        .always($.proxy(this.clearSubmitLoader, this, $form));
    },


    getPath : function($form) {
      return $form.prop("action");
    },

    getParams : function($form) {
      return $form.serialize();
    },

    startSubmitLoader : function($form) {
      $form
        .siblings("h4") // Meh... - easy to break, rethink!
        .attr("data-loading", true);
    },

    clearSubmitLoader : function($form) {
      $form
        .siblings("h4") // Meh... - easy to break, rethink!
        .attr("data-loading", false);
    }

  };

  return UserDropdown;

});

