define('apps/snippets/setupRecentActivity',['require','apps/recentActivity/App'],function(require){
  var RecentActivity = require("apps/recentActivity/App");
  var elm = document.querySelector("[data-app=recent-activity]");

  if (elm) {
    var options_raw = elm.getAttribute("data-options");
    var options = JSON.parse( decodeURIComponent(options_raw) );

    options.el = elm;

    new RecentActivity(options);
  }
});

