define('apps/recentActivity/views/Master',['require','backbone','./Activities','./Loader','./Header','./Controls','./Empty','../Mediator'],function(require){
  var Backbone = require('backbone');

  var Activities = require('./Activities');
  var Loader = require('./Loader');
  var Header = require('./Header');
  var Controls = require('./Controls');
  var Empty = require('./Empty');
  var mediator = require('../Mediator').instance;

  var Master = Backbone.View.extend({
    className: 'recent-activity',
    initialize: function(){
      this.views = {};

      this.views.header = new Header();
      this.views.loader = new Loader();
      this.views.controls = new Controls();
      this.views.empty = new Empty();
      this.views.activities = new Activities({ collection: this.collection });

      this.listenTo(mediator, 'controls:showmore', this.onShowMore, this);

      this.render();
    },
    onShowMore: function() {
      this.collection.fetchMore();
    },
    render: function() {
      var fragment = document.createDocumentFragment();

      fragment.appendChild(this.views.header.el);
      fragment.appendChild(this.views.activities.el);
      fragment.appendChild(this.views.empty.el);
      fragment.appendChild(this.views.loader.el);
      fragment.appendChild(this.views.controls.el);

      this.el.appendChild(fragment);
    }
  });

  return Master;
});

