define('apps/wysiwyg/views/Toolbar',['require','underscore','backbone','../models/Command','./ListButton','./Dropdown','./StandardButton','./Image'],function(require) {

var _ = require("underscore");
var Backbone = require("backbone");

var CommandModel = require("../models/Command");

var ListButtonView = require("./ListButton");
var DropdownView = require("./Dropdown");
var StandardButtonView = require("./StandardButton");
var ImageView = require("./Image");

return Backbone.View.extend({

  attributes: {
    role: "toolbar"
  },

  initialize: function(options) {
    this.editor = options.editor;
    this.views = [];

    var items = this.editor.settings.toolbar.split(" ");

    _.each(items, function(item) {
      switch (item) {
        case "|" : this.buildSeparator(); break;
        case "numlist" : this.buildOlButton(); break;
        case "bullist" : this.buildUlButton(); break;
        case "formats" : this.buildFormatsDropdown(); break;
        case "image" : this.buildImageButton(); break;
        // this will change once we add "i18n"
        // lets just pass "item, item, item" for now
        default : this.buildStandardButton(item, item, item);
      }
    }, this);

    this.render();
  },

  buildSeparator: function() {
    var separator = new Backbone.View({
      tagName: "span",
      attributes: { role: "separator" }
    });

    this.views.push(separator);
  },

  buildOlButton: function() {
    var button = new ListButtonView({
      model: new CommandModel({
        command: "InsertOrderedList",
        name: "numlist",
        label: "Insert numbered list",
        selector: "ol > li"
      })
    });

    this.views.push(button);
  },

  buildUlButton: function() {
    var button = new ListButtonView({
      model: new CommandModel({
        command: "InsertUnorderedList",
        name: "bullist",
        label: "Insert bulleted list",
        selector: "ul > li",
      })
    });

    this.views.push(button);
  },

  buildStandardButton: function(command, name, label) {
    var button = new StandardButtonView({
      model: new CommandModel({
        command: command,
        name: name,
        label: label
      })
    });

    this.views.push(button);
  },

  buildFormatsDropdown: function() {
    var commands = new Backbone.Collection([
      { command: "FormatBlock", value: "p", label: "Normal text" },
      { command: "FormatBlock", value: "pre", label: "Code" },
      { command: "FormatBlock", value: "h2", label: "Header 2" },
      { command: "FormatBlock", value: "h3", label: "Header 3" },
      { command: "FormatBlock", value: "h4", label: "Header 4" }
    ], { model: CommandModel });

    var dropdown = new DropdownView({
      collection: commands,
      label: "Formats"
    });

    this.views.push(dropdown);
  },

  buildImageButton: function() {
    var button = new ImageView({
      model: new CommandModel({
        command: "mceInsertContent",
        label: "Insert image",
        name: "image"
      })
    });

    this.views.push(button);
  },

  render: function() {
    this.$el.append(_.pluck(this.views, "el"));
  }

});

});

