/* globals window */
define('apps/wysiwyg/views/Image',['require','domlib','underscore','i18n','./Command','helpcenter-adapter','../emitter','jquery.fileupload'],function(require) {

var $ = require("domlib");
var _ = require("underscore");
var i18n = require("i18n");
var CommandView = require("./Command");
var HelpCenter = require("helpcenter-adapter");
var emitter = require("../emitter");

require("jquery.fileupload");


return CommandView.extend({

  events: {}, // reset events from CommandView

  render: function() {
    var $input = $("<input type='file' accept='image/gif, image/png, image/jpeg' multiple>");

    this.$el.append($input);

    $input.fileupload({
      // CONFIG
      url: "/hc/user_images",
      dataType : "json",
      type: "POST",
      acceptFileTypes: /(\.|\/)(gif|jpe?g|png)$/i,
      formData: [{
        name: "authenticity_token",
        value: HelpCenter.current_session.csrf_token
      }],
      paramName: "file",
      singleFileUploads: true,
      dropZone : null,
      add: _.bind(this.add, this),
      progress: _.bind(this.progress, this),
      done: _.bind(this.done, this),
      fail: _.bind(this.fail, this)
    });

    this.imageOpacityStart = 0.15;

    emitter.on("upload:filesdropped", function(files) {
      $input.fileupload("add", { files: files });
    });

  },

  add: function(e, data) {
    if (data.files[0].size > 2097152) { // 2097152 => 2MB
      emitter.trigger("upload:error", {
        message: i18n.t("views.shared.image_upload_generic_error", { max_size: 2 })
      });
      return;
    }

    this.insertPreview(data);

    data.submit();
  },

  insertPreview: function(data) {
    if (!window.FileReader) { return; }

    var _this = this;

    data.files.forEach(function(file) {
      var reader = new window.FileReader();

      reader.onload = function(file, e) {
        var dataURL = e.target.result;
        var imageID = _.uniqueId("wysiwyg_image_");
        var value = "<div></div>" + // *1*
          "<div contenteditable=false>" + //  *2*
            "<img id='" + imageID + "' src='" + dataURL + "' style='opacity: " + _this.imageOpacityStart + ";' />" +
          "</div>" +
          "<div></div>"; //  *3*

        // *1* makes sure there's a line break before the image
        // *2* contenteditable=false so we cannot screw up the loader while editing content
        // *3* makes sure there's a line break after the image

        _this.model.set({ value: value });
        _this.execCommand();

        // store the inserted image id for easier tracking
        file.wysiwygImgID = imageID;
      }.bind(this, file);

      reader.readAsDataURL(file);
    });
  },

  progress: function(e, data) {
    var progress = parseInt(data.loaded / data.total * 100, 10);
    var imageID = data.files[0].wysiwygImgID;

    emitter.trigger("upload:progress", {
      progress: progress,
      imageID: imageID
    });
  },

  done: function(e, data) {
    var imageURL = data.result.image.relative_url;
    var imageID = data.files[0].wysiwygImgID;

    if (imageID) {
      // image has already been added to the wysiwyg as preview
      emitter.trigger("upload:completed", {
        imageURL: imageURL,
        imageID: imageID
      });
    } else {
      // browser did not support preview feature so lets add the image now
      this.model.set({ value: "<img src='" + imageURL + "' />" });
      this.execCommand();
      emitter.trigger("upload:completed");
    }
  },

  fail: function(e, data) {
    var imageID = data.files[0].wysiwygImgID;
    var error = data.jqXHR.responseJSON ?
      data.jqXHR.responseJSON.error :
      i18n.t("views.shared.image_upload_generic_error", { max_size: 2 });

    emitter.trigger("upload:error", {
      message: error,
      imageID: imageID
    });
  }

});

});

