/* global window */
define('apps/modalForm',["domlib"], function($) {

  var $iframe,
      $form,
      callback,
      overflow;

  function createIframe() {
    return $("<iframe/>").css({
      border: "none",
      position: "fixed",
      height: "100%",
      width: "100%",
      bottom: "0px",
      top: "0px",
      zIndex: 9999999
    });
  }

  function setup() {
    disableScroll();

    $form = $iframe.contents().find("form[action]"); // XRAY forms in development don't have action attribute.
    $form.submit(submit);

    // Remove the iframe when user clicks the cancel button.
    $iframe
      .contents()
      .find("[data-dismiss]")
      .click(function() {
        enableScroll();
        $iframe.remove();
      });

    // Remove iframe background color if it is set
    $iframe.contents().find("body").css("backgroundColor", "transparent");

    // Caller handles the page specific setup.
    if (callback) {
      callback($form);
    }
  }

  function disableScroll() {
    overflow = document.body.style.overflow;
    document.body.style.overflow = "hidden";
  }

  function enableScroll() {
    document.body.style.overflow = overflow;
  }

  function submit(e) {
    e.preventDefault();
    disable();
    post()
      .always(enable)
      .done(success)
      .fail(error);
  }

  function disable() {
    $form.find(":submit").prop("disabled", true);
  }

  function enable() {
    $form.find(":submit").removeAttr("disabled");
  }

  function post() {
    var path = $form.prop("action"),
        params = $form.serialize();
    return $.post(path, params);
  }

  function success(response) {
    var url = response.redirect_to || window.top.location.href;
    $.when($iframe.fadeOut(200))
      .done(function() {
        window.top.location.href = url;
      });
  }

  function error(request) {
    var html = request.responseText;
    $form.replaceWith(html);
    setup();
  }

  return function(url, setupCallback) {
    callback = setupCallback;
    $iframe = createIframe();

    $iframe
      .attr("src", url)
      .appendTo(document.body)
      .load(setup);
  };
});

