define('pages/enduser/questions',['require','domlib','apps/modalForm','helpcenter-adapter','apps/snippets/disableSubmitButton','apps/snippets/populateVote','nesty'],function (require) {

  var $ = require("domlib");
  var modalForm = require("apps/modalForm");
  var HelpCenter = require("helpcenter-adapter");
  var disableSubmitButton = require("apps/snippets/disableSubmitButton");
  var populateVote = require("apps/snippets/populateVote");
  var Nesty = require("nesty");


  $(function() {

    // Admin modal launchers
    $(document).on("click", ".question-edit, .answer-edit", function(e) {
      modalForm(this.href, function($form) {
        // Called when the form has been loaded, can happen multiple times.
        var $topicSelect = $form.find("[data-js='topicSelect']");
        if ($topicSelect.length) {
          new Nesty($topicSelect[0], { maxWidth: "none" });
        }
      });
      e.preventDefault();
    });

    // Display the answer-delete link to the answer's author.
    var userIdentifier = HelpCenter.user.identifier;
    $(".answer[data-author-identifier=" + userIdentifier + "]")
      .find(".answer-delete")
      .removeAttr("hidden");

    // Disable question form submit button
    var $form = $(".question-form");
    disableSubmitButton($form);

    // Disable answer form submit button
    var $answerForm = $(".answer-form");
    disableSubmitButton($answerForm);

    // Everyone but anonymous
    if (HelpCenter && HelpCenter.user && HelpCenter.user.role !== "anonymous") {

      // Populate vote count
      var path = $(".answer-list-heading").attr("data-callback");
      $.getJSON(path, function(response) { $.each(response.votes, populateVote); } );

      // Populating pending answers
      var $pendingAnswers = $(".pending-answers-list");
      if ($pendingAnswers.length > 0) {
        $pendingAnswers.load($pendingAnswers.attr("data-url"));
      }
    }
  });

});

