define('pages/enduser/articles',['require','domlib','apps/snippets/screencastViewer','helpcenter-adapter','apps/snippets/disableSubmitButton','apps/modalForm','apps/snippets/formatDateTime','tooltip'],function (require) {

  var $ = require("domlib");
  var screencastViewer = require("apps/snippets/screencastViewer");
  var HelpCenter = require("helpcenter-adapter");
  var disableSubmitButton = require("apps/snippets/disableSubmitButton");
  var modalForm = require("apps/modalForm");
  var populatePendingComments = require("apps/snippets/formatDateTime");

  /* Does not return anything */
  require("tooltip");

  screencastViewer();

  $("[rel~=tooltip]").tooltip();

  $("li.comment[data-author-identifier=" + HelpCenter.user.identifier + "]")
    .find("[data-action='delete']").closest(".dropdown").addBack()
    .removeAttr("hidden");

  $("li.comment [data-action='edit']").click(function() {
    modalForm(this.href);
    return false;
  });

  populatePendingComments();

  disableSubmitButton($(".new_comment"));

});

