define('apps/wysiwyg/views/Iframe',['require','backbone','../emitter'],function(require) {

var Backbone = require("backbone");
var emitter = require("../emitter");

return Backbone.View.extend({

  attributes: { role: "group" },

  initialize: function() {
    emitter.on("upload:progress", this.updateProgress, this);
    emitter.on("upload:completed", this.finalizeUpload, this);
    emitter.on("upload:error", this.cleanup, this);
  },

  updateProgress: function(data) {
    // Only perform updates every 5% for better performance
    if (data.progress % 5 !== 0) { return; }

    var $image = this.findInIframe(data.imageID);

    $image.parent().attr({
      "role": "progressbar",
      "aria-valuenow": data.progress
    });

    var opacityStart = data.imageOpacityStart;
    var opacityCurrent = data.progress * 0.01;

    // Only update image opacity if current opacity is actually
    // higher than the image's opacity when added to the iframe
    if (opacityCurrent < opacityStart) { return; }

    var opacityCSS = "opacity:" + opacityCurrent + ";";

    $image.attr({
      "style": opacityCSS,
      "data-mce-style": opacityCSS
    });
  },

  finalizeUpload: function(data) {
    var $image = this.findInIframe(data.imageID);

    $image
      .attr({
        "src": data.imageURL,
        "data-mce-src": data.imageURL,
        "style": "",
        "data-mce-style": ""
      })
      .parent()
      .removeAttr("role")
      .removeAttr("aria-valuenow")
      .removeAttr("contenteditable");
  },

  cleanup: function(data) {
    var $image = this.findInIframe(data.imageID);

    $image.parent().remove();
  },

  findInIframe: function(imageID) {
    return this.$("iframe").contents().find("#" + imageID);
  }

});

});

