define('apps/snippets/setupTinyMCESlim',['require','domlib','tinymce-adapter','apps/wysiwyg/theme'],function(require) {

var $ = require("domlib");
var tinymce = require("tinymce-adapter");
var customTheme = require("apps/wysiwyg/theme");

tinymce.ThemeManager.add("custom", customTheme);

return function(options) {
  options.theme = "custom";
  
  var $textarea = $(options.selector);

  if ($textarea.val()) {
    tinymce.init(options);
  }

  $textarea.one("focus", function() {
    tinymce.init(options);
  });
};

});

