/* global window */

define('pages/enduser/requests',['require','domlib','helpcenter-adapter','apps/snippets/preventInputNumberScroll','apps/snippets/selectRequestIssueType','apps/Screencast','apps/Datepicker','apps/snippets/organizationSubmitter','i18n','apps/snippets/disableSubmitButton','apps/bootstrapNestedDropdowns/bootstrapNestedDropdowns','nesty','upload','jquery.searchbox'],function (require) {

  var $ = require('domlib');
  var HelpCenter = require('helpcenter-adapter');
  var preventInputNumberScroll = require('apps/snippets/preventInputNumberScroll');
  var selectRequestIssueType = require('apps/snippets/selectRequestIssueType');
  var Screencast = require('apps/Screencast');
  var Datepicker = require('apps/Datepicker');
  var submitOrganization = require('apps/snippets/organizationSubmitter');
  var i18n = require('i18n');
  var disableSubmitButton = require('apps/snippets/disableSubmitButton');
  var bootstrapNestedDropdowns = require('apps/bootstrapNestedDropdowns/bootstrapNestedDropdowns');
  var Nesty = require('nesty');

  /* This only extends jQuery.fn/prototype */
  require('upload');
  require('jquery.searchbox');

  $('.form-field select').each(function() {
    new Nesty(this);
  });

  bootstrapNestedDropdowns();

  preventInputNumberScroll();

  selectRequestIssueType();

  $('.satisfaction-box').on('click', '[for="satisfaction_rating_score_good"]', function(){
    $('#satisfaction_rating_score_bad').removeAttr('checked');
    $('#satisfaction_rating_score_good').attr('checked', 'checked');
  });

  $('.satisfaction-box').on('click', '[for="satisfaction_rating_score_bad"]', function(){
    $('#satisfaction_rating_score_good').removeAttr('checked');
    $('#satisfaction_rating_score_bad').attr('checked', 'checked');
  });

  new Screencast($('#request-screencasts'));

  $('#quick-search').keypress(function(e) {
    if(e.which === 13) {
      getRequestSearch();
    }
  });

  $('#request-status-select, #request-organization-select').change(function() {
    getRequestSearch();
  });

  function getRequestSearch(){
    var searchString = $('#quick-search').val();
    var statusFilter = $('#request-status-select').val();
    var organizationFilter = $('#request-organization-select').val();

    var queryString = 'query=' + searchString + '&status=' + statusFilter;

    if (organizationFilter) {
      queryString += '&organization_id=' + organizationFilter;
    }

    window.location.search = queryString;
  }

  $('[data-datepicker]').each(function() {
    new Datepicker(this);
  });

  $('#request_type').change(function(e){
    var target = e.target || e.srcElement,
        showOrHide = target.value === 'task';
    $('[data-due-date]').toggle(showOrHide);
  });

  $('#request-organization select').change(submitOrganization);

  $(function() {

    /*
      Autosuggestions
    */

    var searchUrl = '/hc/api/v2/search.json?per_page=4&locale=' +
      HelpCenter.current_session.locale + '&query={{query}}';

    $('[data-searchbox]').searchbox({
      url: searchUrl,
      title: i18n.t('txt.help_center.views.requests.suggested_solutions'),
      eventName: 'keyup',
      threshold: 500,
      minChars: 3,
      url_filter: function(url) {
        var value = $('[data-searchbox] input').val();
        value = $.trim(value);
        var result = url + '?input_string=' + encodeURIComponent(value);
        return result;
      }
    });

    /*
      This delay the form submission if a file is being uploaded!
    */

    var $upload = $('#request-attachments'),
        $form = $upload.closest('form'),
        $submit = $form.find('[type=submit]'),
        isUploading = false,
        submitFormOnUploadComplete = false;


    function onSubmit(e) {
      if (isUploading) {
        e.preventDefault();
        $submit.prop('disabled', true);
      }
      submitFormOnUploadComplete = true;
    }

    function onUploadStart() {
      isUploading = true;
    }

    function onUploadStop() {
      isUploading = false;
      if (submitFormOnUploadComplete) {
        $form.submit();
      }
    }

    //Bind events
    $upload.on('fileuploadstart', onUploadStart);
    $upload.on('fileuploadstop', onUploadStop);
    $form.on('submit', onSubmit);
    disableSubmitButton($form);

    //Initialize
    $upload.upload();
  });

});

