define('apps/helpers/extend',[],function() {

  function extend(destination) {
    var args, i, l;

    destination = destination || {};
    args = [].slice.call(arguments, 1);

    for (i=0, l=args.length; i<l; i++) {
      var arg, key;
      arg = args[i];

      for (key in arg) {
        if (arg.hasOwnProperty(key)) {
          destination[key] = arg[key];
        }
      }
    }

    return destination;
  }

  return extend;
});

