define('apps/bootstrapNestedDropdowns/bootstrapNestedDropdowns',[
  "domlib",
  "i18n",
  "nesty"
], function($, i18n, Nesty) {

  return function() {

    $("[data-tagger]").each(function() {
      var $taggerInput = $(this),
          tree = $taggerInput.data("tagger"),
          nesty = new Nesty({ options: tree }, { backLabel: i18n.t("txt.help_center.views.admin.shared.back") });

      $taggerInput.after(nesty.input.el);
      nesty.on("change", function(option) {
        $taggerInput.val(option.value);

        var event;
        if (document.createEvent) {
          event = document.createEvent("HTMLEvents");
          event.initEvent("change", true, true);
          $taggerInput[0].dispatchEvent(event);
        } else {
          event = document.createEventObject();
          event.eventType = "change";
          $taggerInput[0].fireEvent("on" + event.eventType, event);
        }
      });
    });
  };
});

