(function (factory) {
  if (typeof define === 'function' && define.amd) {
    // AMD. Register as an anonymous module.
    define('jquery.searchbox',['jquery'], factory);
  } else {
    // Browser globals
    factory(jQuery);
  }
}(function($) { 'use strict';

  $.fn.searchbox = function (options) {

    this.each(function() {
      var $this, $input, $searchbox, $tmpl, settings, timer, cache = {}, request = {};

      settings = $.extend({
        input: '#request_subject',
        eventName: 'keyup',
        minChars: 2,
        threshold: 300, //ms
        title: '',
        url: '',
        url_filter: function(result) { return result; }
      }, options);

      $this = $(this);
      $input = settings.input instanceof $ ? settings.input : $(settings.input);

      //Only initialize once!
      if ($this.data('searchbox')) {
        return true;
      } else {
        $this.data('searchbox', true);
        $searchbox = $('<div class="searchbox-suggestions"></div>');

        $tmpl = $('<div class="searchbox"><label>'+settings.title+'</label></div>').hide();
        $tmpl.append( $searchbox );

        $this.append( $tmpl );
      }

      function hide() {
        $tmpl.stop().slideUp('fast');
      }

      function show() {
        $tmpl.stop().slideDown('fast');
      }

      function onBeforeSend() {
        $this.attr('data-loading', true);
      }

      function onSuccess(json) {
        var items = [];

        //Create new HTML
        $this.attr('data-loading', false);

        //Build innerHTML
        $.each(json.results, function(index, result) {
          var $link = $('<a/>', {
            href: settings.url_filter(result.html_url),
            text: result.name
          })
          var $item = $('<li/>').html($link);
          items.push($item);
        });

        var $list = $('<ul/>').html(items);

        $searchbox.empty();
        $searchbox.html($list);
        if (items.length > 0) {
          show();
        } else {
          hide();
        }
      }

      function doSearch() {
        var query, queryEncoded, urlCompiled, minCharsReached, firstLetter;

        query = $.trim($input.val());
        query = query.toLowerCase();
        firstLetter = query.charCodeAt(0);
        queryEncoded = encodeURIComponent(query);
        urlCompiled = settings.url.replace('{{query}}', queryEncoded);

        if (request.readyState === 1) {
          request.abort();
          $this.attr('data-loading', false);
        }

        /*
         * Special case considering CJK characters. Since ideographs represent
         * whole words, we want to start searching when just two has been typed.
         *
         * Unicode range reference:
         * http://www.unicode.org/versions/Unicode5.0.0/ch12.pdf#G16616
         */
        if (firstLetter >= 0x4E00 && firstLetter <= 0x2FA1F) {
          minCharsReached = query.length >= 2;
        } else {
          minCharsReached = query.length >= settings.minChars;
        }

        if (minCharsReached) {
          //Use cache or make request
          if (cache[query]) {
            onSuccess(cache[query]);
          } else {
            request = $.ajax(urlCompiled, {
              beforeSend: onBeforeSend,
              success: function(json) {
                cache[query] = json;
                onSuccess.apply(this, arguments);
              }
            });
          }
        }
      }

      //When the event is fired
      function onEvent() {
        if (timer) {
          clearTimeout(timer);
        }

        timer = setTimeout(doSearch, settings.threshold);
      }

      //Bind events
      $input.on(settings.eventName, onEvent);
    });

    return this;

  };
}));

