/* global window */
define('apps/Screencast',["domlib", "i18n"], function($, i18n) {

  var Screencast = function($recorder) {
    if (!$recorder.length) {
      return;
    }

    this.$recorder = $recorder;
    this.$pool = $("#" + this.$recorder.data("pool"));

    var templateHTML = $("#" + this.$pool.data("template")).html();
    this.$template = $($.trim(templateHTML));

    this.recorderId = this.$recorder.data("recorder-id");
    this.host = this.$recorder.data("host");
    this.name = this.$recorder.data("name");
    this.counter = 0;
    this.screencastI18nName = i18n.t("txt.help_center.javascripts.screencasts.name");
    this.init();
  };

  Screencast.prototype = {
    init : function() {
      var url, callback;

      this.populate();
      this.events();

      url = this.screenrUrl();
      callback = $.proxy(this.setupScreenr, this);

      $.getScript(url, callback);
    },

    populate : function() {
      var screencasts = this.$pool.data("screencasts"),
          _this = this;

      $.each(screencasts || [], function(i, screencast) {
        if(screencast.position > _this.counter) {
          _this.counter = screencast.position - 1;
        }
        _this.append(screencast);
      });
    },

    events : function() {
      this.$recorder.click(function(e) { e.preventDefault(); });
      this.$pool.on("click", "[data-upload-remove]", $.proxy(this.remove, this));
    },

    remove : function(e) {
      $(e.target).closest("[data-upload-item]").remove();
    },

    screenrUrl : function() {
      var hostname = this.host.replace(/.*?:\/\//g, ""),
          protocol = document.location.protocol + "//";

      return protocol + hostname + "/api/recorder";
    },

    setupScreenr : function() {
      if (!window.Screenr) { return; } //Screenr wasn't loaded - halt!

      var screenr = window.Screenr,
          recorder = screenr.Recorder({
            id: this.recorderId,
            hideAllFields: true,
            scrollToTop: true
          });

      recorder.attachTo(this.$recorder.attr("id"));

      recorder.setUserName("", false); //sets and hides field
      recorder.setUserEmail("", false);
      recorder.setSubject("", false);
      recorder.setDescription("", false);

      recorder.setOnComplete($.proxy(this.append, this));

      // Comment above and uncomment below in case
      // you want to by-pass Screenr for debugging

      // var self = this;
      // this.$recorder.click( function() {
      //   self.append({
      //     id: Date.now(),
      //     embedUrl: 'https://support1.viewscreencasts.com/embed/f3d6ed9c9df342208c669fba1a1e5aaa',
      //     thumbnailUrl : ''
      //   });
      // });
    },

    append : function(screencast) {
      this.counter += 1;

      var $screencast = this.render({
        id : screencast.id,
        content_url : screencast.content_url || screencast.embedUrl,
        thumbnail_url : screencast.thumbnail_url || screencast.thumbnailUrl,
        position : screencast.position || this.counter
      });

      this.$pool.append($screencast);
    },

    render : function(screencast) {
      var $screencast = this.$template.clone();

      $screencast.addClass("upload-item-screencast");

      $screencast
        .find("[data-upload-link]")
        .attr("href", screencast.content_url)
        .text(this.screencastI18nName + screencast.position);

      $screencast
        .find("input:hidden")
        .attr("name", this.name)
        .val(JSON.stringify(screencast));

      return $screencast;
    }

  };

  return Screencast;

});

