define('apps/wysiwyg/views/Command',['require','backbone','../emitter'],function(require) {

var Backbone = require("backbone");
var emitter = require("../emitter");

return Backbone.View.extend({

  tagName: "span",

  className: function() {
    return "wysiwyg-icon-" + this.model.get("name");
  },

  attributes: { role: "button" },

  events: { "click": "execCommand" },

  initialize: function() {
    emitter.on("editor:ready", this.saveEditor, this);
    emitter.on("editor:ready", this.onEditorReady, this);

    this.render();
  },

  saveEditor: function(editor) {
    this.editor = editor;
  },

  // override this method whe extending from this View
  onEditorReady: function() {},

  execCommand: function() {
    var command = this.model.get("command");
    var value = this.model.get("value");

    this.editor.execCommand(command, false, value);
  }

});

});

