define('apps/snippets/screencastViewer',['domlib'], function($) {

  return function() {
    var width='570';
    var height='340';

    // Convert all Screencasts links in forums into an iframe showing the video.
    $('.zendesk-embedded-screencast').replaceWith(function() {
      var screencastUrl = $(this).data('screencastUrl'),
          $iframe = $('<iframe width="' + width + '" height="' + height + '"/>');
      return $iframe.attr('src', screencastUrl);
    });
  };

});

