define('apps/recentActivity/views/Header',['require','backbone','i18n'],function(require){
  var Backbone = require('backbone');
  var i18n = require('i18n');

  return Backbone.View.extend({
    tagName: 'h2',
    className: 'recent-activity-header',
    initialize: function() {
      this.render();
    },
    render: function() {
      this.$el.text(i18n.t('views.admin.shared.recent_activity.title'));
    }
  });
});

