/* globals window */
define('apps/snippets/PostMessage',['require','apps/helpers/bind','apps/helpers/addEvent'],function(require) {
  var bind = require('apps/helpers/bind');
  var listen = require('apps/helpers/addEvent');

  function PostMessage(source, origin) {
    this.win = window;
    this.source = source;
    this.origin = origin || document.referrer;
    this._events  = {};

    listen(this.win, 'message', bind(this.onMessage, this));
  }

  PostMessage.prototype = {
    setOrigin: function(origin) {
      this.origin = origin;
    },
    setSource: function(source) {
      this.source = source;
    },
    onMessage: function(evt) {
      var msg, data, json;

      //Safety check
      if (evt.source !== this.source) { return; }

      // Deserialize the message and data
      json = JSON.parse(evt.data);
      msg = json.message;
      data = json.data;

      this.emit.call(this, msg, data);
    },
    emit: function(name, data) {
      var callbacks = this._events[name] || [];
      for (var i=0,l=callbacks.length; i<l; i++) {
        var callback = callbacks[i];
        callback.cb.call(callback.context, data);
      }
    },
    on: function(name, cb, context) {
      var obj = { cb: cb, context: context };

      if (!this._events[name]) {
        this._events[name] = [obj];
      } else {
        this._events[name].push(obj);
      }
    },
    send: function(message, data) {
      var jsonstr, origin, obj;

      obj = {
        message: message || '',
        data: data || {},
      };

      jsonstr = JSON.stringify(obj);
      origin = this.origin;

      this.source.postMessage(jsonstr, origin);
    }
  };

  return PostMessage;
});

