define('apps/snippets/populateVote',["domlib"], function($) {

  return function(index, vote) {

    var $voteControls = $("section.answers div.vote-controls[data-record-id=" + vote.dom_id + "]"),
        $upvote = $voteControls.find("a.vote-up"),
        $downvote = $voteControls.find("a.vote-down");

    if (vote.direction === "up") {
      $upvote.attr({ "data-method": "delete", "aria-selected": true });
      $downvote.attr({ "data-method": "put" });
    } else if (vote.direction === "down") {
      $downvote.attr({ "data-method": "delete", "aria-selected": true });
      $upvote.attr({ "data-method": "put" });
    }

  };

});

