define('apps/wysiwyg/views/Container',['require','backbone','./Toolbar','./Iframe','./Dropzone','./Error'],function(require) {

var Backbone = require("backbone");
var ToolbarView = require("./Toolbar");
var IframeView = require("./Iframe");
var DropzoneView = require("./Dropzone");
var ErrorView = require("./Error");

return Backbone.View.extend({

  id: "hc-wysiwyg",

  attributes: { role: "application" },

  initialize: function(options) {
    this.views = {
      toolbar: new ToolbarView({ editor: options.editor }),
      iframe: new IframeView(),
      dropzone: new DropzoneView(),
      error: new ErrorView()
    };

    this.render();
  },

  render: function() {
    this.$el.append([
      this.views.toolbar.el,
      this.views.iframe.el,
      this.views.dropzone.el,
      this.views.error.el
    ]);
  }

});

});

