define('apps/dropdowns',['require','domlib'],function(require) {

var $ = require("domlib");

function dismiss() {
  $(".dropdown-menu").attr("aria-expanded", false);
}

function toggle() {
  var $menu = $(this).siblings(".dropdown-menu");
  var expanded = $menu.attr("aria-expanded") === "true";
  $menu.attr("aria-expanded", !expanded);
  return false;
}

function stop(e) {
  e.stopPropagation();
}

function listen() {
  $(document)
    .on("click", dismiss)
    .on("click", ".dropdown-toggle", dismiss)
    .on("click", ".dropdown-toggle", toggle)
    .on("click", ".dropdown-menu", stop)
    .on("click", ".dropdown-menu [role='menuitem']", dismiss);
}

return listen;

});

