define('apps/wysiwyg/views/Error',['require','underscore','backbone','../emitter','text!../templates/error.html'],function(require) {

var _ = require("underscore");
var Backbone = require("backbone");
var emitter = require("../emitter");
var template = require("text!../templates/error.html");

return Backbone.View.extend({

  attributes: { role: "alert", "aria-hidden": true },

  template: _.template(template),

  initialize: function() {
    emitter.on("upload:error", this.error, this);
    emitter.on("upload:completed", this.success, this);
  },

  render: function(message) {
    this.$el.html(this.template({ message: message }));
  },

  error: function(data) {
    var message = data.message;

    this.render(message);
    this.$el.attr("aria-hidden", false);
  },

  success: function() {
    this.$el.attr("aria-hidden", true);
  }

});

});

