/* global window */
define('apps/sso/Host',['require','./windowMgr','apps/helpers/extend','apps/snippets/PostMessage'],function(require) {
  var windowMgr = require('./windowMgr');
  var extend = require('apps/helpers/extend');
  var PostMessage = require('apps/snippets/PostMessage');

  function SSO(options) {
    this.options = extend({
      return_to: this.getSelfUrl(),
      auth_domain: this.getSelfUrl(),
      frame_url: this.getSelfUrl(),
      locale: 'en-us',
      token: null,
      brand_id: null,
      class_name: ''
    }, options);

    this.message = new PostMessage();
    this.message.origin = this.options.frame_url;
    this.message.on('sso:close', this.onClose, this);
    this.message.on('sso:ready', this.onReady, this);

    this.windowMgr = windowMgr;
  }

  SSO.prototype = {
    //Routes to zendesk_auth mapping
    ACTIONS: {
      'signin': 'login',
      'email_verification': 'email-verification',
      'reset_password': 'password-set'
    },

    getSelfUrl: function() {
      var loc = window.location;

      return [
        loc.protocol,
        '//',
        loc.hostname,
        (loc.port ? ':' + loc.port : '')
      ].join('');
    },

    load: function(action) {
      var hash, locale, baseUrl, url, iframe;

      action = action || 'signin';
      hash = '#' + this.ACTIONS[action];
      locale = this.options.locale;

      baseUrl = [this.options.frame_url, 'hc', locale, 'sso'].join('/'); // /hc/en-us/sso

      url = baseUrl + hash;

      iframe = this.windowMgr.create(url);
      this.message.setSource( iframe.contentWindow );
    },

    reload: function() {
      window.location.href = this.options.return_to;
    },

    onClose: function() {
      this.windowMgr.destroy();
    },

    onReady: function(data) {
      if (data.signed_in) {
        this.reload();
      } else {
        this.message.send('sso:load_script', {
          url: this.options.auth_domain + '/auth/login?',
          token: this.options.token,
          social_return_to: this.options.return_to,
          brand_id: this.options.brand_id,
          class_name: this.options.class_name
        });
      }
    }
  };

  return SSO;
});

