define('apps/recentActivity/views/Empty',['require','backbone','i18n','../Mediator'],function(require) {
  var Backbone = require('backbone');
  var i18n = require('i18n');
  var mediator = require('../Mediator').instance;

  return Backbone.View.extend({
    className: 'recent-activity-no-activities',
    initialize: function() {
      this.render();
      this.listenTo(mediator, 'pagination:no_pages', this.onNoPages, this);
      this.listenTo(mediator, 'activities:loaderror', this.onLoadError, this);
    },
    onLoadError: function(collection) {
      // We only want to show this if we havn't already
      // loaded activites
      if (collection.pagination.get('page') === 0) {
        this.$el.show();
      }
    },
    onNoPages: function() {
      this.$el.show();
    },
    render: function() {
      var text = i18n.t('views.admin.shared.recent_activity.no_activities');
      this.$el.hide();
      this.$el.text(text);
    }
  });
});

