/* global window */
define('apps/snippets/populatePendingComments',['require','domlib','helpcenter-adapter','apps/snippets/formatDateTime'],function(require) {

var $ = require("domlib");
var HelpCenter = require("helpcenter-adapter");
var formatDateTime = require("apps/snippets/formatDateTime");

return function() {

  if (HelpCenter && HelpCenter.user && HelpCenter.user.role !== "anonymous") {

    $(".comment-list[data-url]").each(function() {
      $.get(this.getAttribute("data-url"), $.proxy(function(data) {
          var $comments = $(data);
          $comments.appendTo(this);
          formatDateTime();
          $comments.each(function() {
            if (window.location.hash !== "#" + this.id) { return; }
            // clean the hash first or nothing will happen
            window.location.hash = "";
            window.location.hash = this.id;
          });
        }, this));
    });
    
  }

};

});

