define('apps/recentActivity/models/Activity',['require','backbone','underscore'],function(require){
  var Backbone = require('backbone');
  var _ = require('underscore');

  return Backbone.Model.extend({
    defaults: {
      'id': null,
      'action': '',
      'title': '',
      'url': '#',
      'timestamp': '',
      'comment_count': null,
      'breadcrumbs': []
    },
    validate: function(attrs) {
      var RECENT_ACTIVITY = [
        'article_created',
        'article_updated',
        'article_comment_added',
        'post_created',
        'post_updated',
        'post_comment_added',
        'question_created',
        'question_updated',
        'answer_added'
      ];

      if (_.indexOf(RECENT_ACTIVITY, attrs.action) < 0) {
        return 'Unknown action type';
      }
    }
  });
});

