/* global window */
define('apps/wysiwyg/views/Dropzone',['require','backbone','../emitter'],function(require) {

var Backbone = require("backbone");
var emitter = require("../emitter");

return Backbone.View.extend({

  tagName: "a",

  attributes: {
    "aria-dropeffect": "execute",
    "aria-hidden": "true",
    "title": "Drop file here"
  },

  initialize: function() {
    if (!(window.File && window.FileList && window.FileReader)) { return; }

    var xhr = new XMLHttpRequest();

    if (!xhr.upload) { return; }

    document.addEventListener("dragover", this.toggle.bind(this));
    document.addEventListener("drop", function(e) { e.preventDefault(); });

    this.el.addEventListener("drop", this.drop.bind(this), false);
  },

  toggle: function(e) {
    e.preventDefault();

    if (!this.timeout) { this.$el.attr("aria-hidden", false); }
    else { clearTimeout(this.timeout); }

    this.timeout = setTimeout(function () {
      this.timeout = null;
      this.$el.attr("aria-hidden", true);
    }.bind(this), 100);
  },

  drop: function(e) {
    e.stopPropagation();
    e.preventDefault();

    var files = e.target.files || e.dataTransfer.files;

    emitter.trigger("upload:filesdropped", files);
  }

});

});

