define('apps/recentActivity/views/Controls',['require','backbone','underscore','i18n','text!../templates/controls.html','../Mediator'],function(require){
  var Backbone = require('backbone');
  var _ = require('underscore');
  var i18n = require('i18n');
  var template = require('text!../templates/controls.html');
  var mediator = require('../Mediator').instance;

  return Backbone.View.extend({
    className: 'recent-activity-controls',
    initialize: function() {
      this.listenTo(mediator, 'activities:loadstart', this.hide, this);
      this.listenTo(mediator, 'activities:loaderror', this.hide, this);
      this.listenTo(mediator, 'pagination:no_pages', this.hide, this);
      this.listenTo(mediator, 'pagination:changed', this.onPaginationChange, this);
      this.template = _.template(template);
      this.render();
    },
    events: {
      'click [data-recent-activity-seemore]': 'onClick'
    },
    hide: function() {
      this.$el.hide();
    },
    show: function() {
      this.$el.show();
    },
    onPaginationChange: function(model) {
      if (model.get('page') === model.get('page_count')) {
        this.hide();
      } else {
        this.show();
      }
    },
    onClick: function(e) {
      e.preventDefault();
      mediator.trigger('controls:showmore');
    },
    render: function() {
      this.el.innerHTML = this.template({
        see_more: i18n.t('views.admin.shared.recent_activity.see_more')
      });
    }
  });
});

