define('apps/notification/App',[
  './models/Notification',
  './views/Notification'
], function(NotificationModel, NotificationView) {
  return function(data) {

    var model = new NotificationModel(data),
        view = new NotificationView({ model: model });

    // Expose simple API with a few options
    // to manipulate the notification
    return {

      set: function(options) {
        if (options) {
          model.set({
            message: options.message || model.get('message'),
            type: options.type || model.get('type'),
            layout: options.layout || model.get('layout'),
            hidden: options.hidden !== undefined ? options.hidden : model.get('hidden')
          });
        }
        return this;
      },

      appendTo: function(anchor) {
        view.$el.appendTo(anchor);
        return this;
      },

      prependTo: function(anchor) {
        view.$el.prependTo(anchor);
        return this;
      },

      detach: function() {
        view.$el.detach();
        return this;
      },

      dismiss: function() {
        view.dismiss();
      },

      // for testing only
      $el: view.$el

    };

  };
});

