define('apps/Datepicker',[
  'domlib',
  'moment',
  'pikaday',
  'i18n'
], function($, moment, Pikaday, i18n) {

  var SYSTEM_FORMAT = 'YYYY-MM-DD';
  var localizedFormat = i18n.t('date.datepicker.date_format');

  // Create intermediary format from the jQuery UI Datepicker format
  // http://api.jqueryui.com/datepicker/#utility-formatDate
  var intermediaryFormat = localizedFormat
        .replace('dd', '_01_')
        .replace('d',  '_02_')
        .replace('oo', '_03_')
        .replace('o',  '_04_')
        .replace('DD', '_05_')
        .replace('D',  '_06_')
        .replace('mm', '_07_')
        .replace('m',  '_08_')
        .replace('MM', '_09_')
        .replace('M',  '_10_')
        .replace('yy', '_11_')
        .replace('y',  '_12_');

  // Create a Moment JS format from the intermediary format
  // http://momentjs.com/docs/#/displaying/
  var userFormat = intermediaryFormat
        .replace('_01_', 'DD')
        .replace('_02_', 'D')
        .replace('_03_', 'DDDD')
        .replace('_04_', 'DDD')
        .replace('_05_', 'dddd')
        .replace('_06_', 'ddd')
        .replace('_07_', 'MM')
        .replace('_08_', 'M')
        .replace('_09_', 'MMMM')
        .replace('_10_', 'MMM')
        .replace('_11_', 'YYYY')
        .replace('_12_', 'YY');

  function Datepicker(input) {
    this.$input = $(input);
    this.$datepicker = $([]); // cached when rendered

    this.render();
    this.obfuscate();
    this.pikaday = this.initPikaday();
  }

  Datepicker.prototype = {

    obfuscate : function() {
      this.$input
        .attr({
          'class': '',
          'readonly' : true
        })
        .hide();
    },

    render : function() {
      this.$datepicker = $('<input/>', {
        'class' : this.$input.attr('class'),
        'value' : this.localizedDate(),
        'type'  : 'text'
      }).insertBefore(this.$input);
    },

    localizedDate : function() {
      var value = this.$input.val();
      return value.length ?
        moment(value, SYSTEM_FORMAT).format(userFormat) :
        '';
    },

    initPikaday : function() {
      return new Pikaday({
        field              : this.$datepicker[0],
        format             : userFormat,
        onSelect           : $.proxy(this.formatAndSync, this),
        firstDay           : Number(i18n.t('date.datepicker.first_day')),
        isRTL              : Boolean(Number(i18n.t('date.datepicker.is_rtl'))),
        showMonthAfterYear : Boolean(Number(i18n.t('date.datepicker.show_month_after_year'))),
        yearSuffix         : i18n.t('date.datepicker.year_suffix'),
        i18n               : {
          previousMonth : i18n.t('date.datepicker.prev_text'),
          nextMonth     : i18n.t('date.datepicker.next_text'),
          months        : i18n.t('date.month_names').slice(1),
          weekdays      : i18n.t('date.min_day_names'),
          weekdaysShort : i18n.t('date.min_day_names')
        }
      });
    },

    formatAndSync : function() {
      var selectedDate = this.pikaday.getDate(),
          parsedDate = moment(selectedDate).format(SYSTEM_FORMAT);
      this.$datepicker.val(moment(parsedDate, SYSTEM_FORMAT).format(userFormat));
      this.$input.val(parsedDate);
    }

  };

  return Datepicker;

});

