define('apps/notification/views/Notification',["backbone"], function(Backbone) {

  return Backbone.View.extend({

    attributes: function() {
      return {
        "class": [
          "notification",
          "notification-" + this.model.get("type"),
          (this.model.get("layout") === "inline" ? "notification-inline": "")
        ].join(" "),
        "aria-hidden": this.model.get("hidden")
      };
    },

    initialize: function() {
      this.currentType = this.model.get("type");
      this.listenTo(this.model, "change:type", this.changeType);
      this.listenTo(this.model, "change:hidden", this.changeVisibility);
      this.listenTo(this.model, "change:message", this.changeMessage);
      this.listenTo(this.model, "change:layout", this.toggleLayout);
      this.render();
    },

    events: {
      "click .notification-dismiss" : "dismiss"
    },

    render: function() {
      var message = this.createElement("span", { "class": "notification-text" });
      message.innerText = message.textContent = this.model.get("message");
      this.$el.append(message);

      if (this.model.get("icon")) {
        var icon = this.createElement("span", { "class": "notification-icon" });
        this.$el.prepend(icon);
      }

      if (this.model.get("dismiss")) {
        var dismiss = this.createElement("a", { "class": "notification-dismiss", href: "#" });
        this.$el.append(dismiss);
      }

      if (this.model.get("layout") === "flash") { this.flashify(); }
    },

    createElement: function(tagName, attrs) {
      var element, attr;

      element = document.createElement(tagName);
      for (attr in attrs) {
        if (attrs.hasOwnProperty(attr)) {
          element.setAttribute(attr, attrs[attr]);
        }
      }
      return element;
    },

    flashify: function() {
      // wrap the inner elements of the notification in a div tag with the
      // class "notification-inner" to make it look like a flash message
      var inner = this.createElement("div", { "class": "notification-inner" });
      this.$el.wrapInner(inner);
    },

    unflashify: function() {
      var html = this.$el.find(".notification-inner").html();
      this.$el.html(html);
    },

    dismiss: function() {
      this.$el.remove();
      this.stopListening();
    },

    changeMessage: function() {
      this.$el
        .find(".notification-text")
        .text(this.model.get("message"));
    },

    changeType: function() {
      this.$el.toggleClass([
        "notification-" + this.currentType,
        "notification-" + this.model.get("type")
      ].join(" "));
      this.currentType = this.model.get("type");
    },

    changeVisibility: function() {
      this.$el.attr("aria-hidden", this.model.get("hidden"));
    },

    toggleLayout: function() {
      var newLayout = this.model.get("layout");
      if (newLayout === "inline") {
        this.unflashify();
        this.$el.addClass("notification-inline");
      } else if (newLayout === "flash") {
        this.flashify();
        this.$el.removeClass("notification-inline");
      } else {
        this.unflashify();
        this.$el.removeClass("notification-inline");
      }
    }

  });

});

