define('apps/recentActivity/models/Pagination',['require','backbone','../Mediator'],function(require) {
  var Backbone = require('backbone');
  var mediator = require('../Mediator').instance;

  return Backbone.Model.extend({
    defaults: {
      page: 0,
      previous_page: null,
      next_page: null,
      per_page: 5,
      page_count: null,
      count: 0
    },
    initialize: function() {
      this.listenTo(this, 'change:page_count', this.onChangePageCount, this);
      this.listenTo(this, 'change:page', this.onChangePage, this);
    },
    onChangePageCount: function() {
      if (this.get('page_count') === 0) {
        mediator.trigger('pagination:no_pages', this);
      }
    },
    onChangePage: function() {
      if (this.get('page_count') > 0) {
        mediator.trigger('pagination:changed', this);
      }
    }
  });
});

