define('apps/sso/windowMgr',[],function() {
  var iframe, STYLES, events;

  events = {};

  STYLES = {
    'z-index': '9999',
    'display': 'block',
    'background-color': 'transparent',
    'border': '0px none transparent',
    'overflow-x': 'hidden',
    'overflow-y': 'auto',
    'visibility': 'visible',
    'margin': '0',
    'padding': '0',
    '-webkit-tap-highlight-color': 'transparent',
    'position': 'fixed',
    'left': '0',
    'top': '0',
    'width': '100%',
    'height': '100%'
  };

  function getStyleStr(useImportant) {
    var rule, closure, style = '';

    closure = useImportant ? ' !important;' : ';';

    for (rule in STYLES) {
      if (STYLES.hasOwnProperty(rule)) {
        style += rule + ':' + STYLES[rule] + closure;
      }
    }

    return style;
  }

  function getBody() {
    return document.getElementsByTagName('body')[0];
  }

  function createIframe(src, onLoad) {
    var style, body;

    src = src || '#';
    onLoad = onLoad || function(){};
    style = getStyleStr(true);
    body = getBody();

    if (iframe) {
      iframe.onload = onLoad;
      iframe.setAttribute('src', src);
    } else {
      iframe = document.createElement('iframe');
      iframe.onload = onLoad;
      iframe.setAttribute('style', style);
      iframe.setAttribute('src', src);

      body.appendChild(iframe);
    }

    return iframe;
  }

  function destroy() {
    var body = getBody();

    body.removeChild(iframe);
    iframe = undefined;
  }

  return {
    create: createIframe,
    destroy: destroy
  };

});

