define('apps/wysiwyg/theme',['require','underscore','./views/Container','text!./content.css','./emitter'],function(require) {

var _ = require("underscore");
var ContainerView = require("./views/Container");
var contentStyle = require("text!./content.css");
var emitter = require("./emitter");

function theme(emitter, editor) {
  var textarea = editor.targetElm;
  var container = new ContainerView({ editor: editor });

  // setup defaults
  _.defaults(editor.settings, {
    content_style: contentStyle,
    browser_spellcheck: true,
    auto_focus: editor.id,
    height: 175,
    inline: true
  });

  // override defaults
  _.extend(editor.settings, {
    relative_urls: false
  });

  container.$el.insertAfter(textarea);

  // notify everyone insterested when the editor is ready
  editor.on("init", function() {
    emitter.trigger("editor:ready", editor);
  });

  // This gets called by the editor.
  this.renderUI = function() {
    return {
      editorContainer: container.el,
      iframeContainer: container.views.iframe.el,
    };

  };

}

// pass in the emitter singleton
return _.bind(theme, null, emitter);

});

