define('apps/notification/models/Notification',["backbone"], function(Backbone) {
  return Backbone.Model.extend({
    defaults: {
      message: "",
      type: "notice", // "notice", "error", "alert", "lock"
      icon: true,
      dismiss: true,
      hidden: false,
      layout: "normal", // "normal", "flash", "inline"
      flash: false // rails flash style
    }
  });
});

